*{
  background-color: black;
  color: white;
}

.App{
   width:80vw;
   height: 80vh;
   border: 1px solid black;
   margin-left: 10%;
   margin-top: 5%;

}

.headerContainer{
  width:80vw;
  height:30px;
  /* border: 1px solid red; */
  display: flex; 
}

.brandname, .logo{
  margin: 0;
  padding: 0;
}

.brandname{
  font-family:Arial, Helvetica, sans-serif;
  font-size: 35px;
}

.logo{
  margin-left: 800px;
  font-size: 25px;
}

/* Typing container styling */

.typingContainer{
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-top:30px;
}

.manualtimebtn >button {
  font-size: 20px;
  border: none;
}

.textbox{
  margin-top: 20px;
  font-size: 25px;
  font-family: sans-serif;
  opacity: 0.4;
  line-height: 35px;
  
}

/* Footer styling     part*/

.footer{
  margin-top:40px;
}
.footerIcon{
    margin-left: 50%;
    border: none;
    font-size: 20px;
    margin-top: 20px;
}

.escbtn{
  margin-left: 50%;
  font-size: 20px;
  background-color: white;
  color:black;
  border: 1px solid black;
 border-radius: 10px;
 margin-top: 10px;

} 

.wordsbtn{
  margin-left: 44%;
  display: flex;
  gap:10px;
  
}

.wordsbtn >button{
  background-color: white;
  color: black;
  border: none;
  border-radius: 10px;
  margin-top: 10px;
  font-size: 15px;
}

.wordsbtn >span{
  margin-top:10px;
}

.socialmedia{
  display: flex;
  justify-content: space-between;
}

.socialicon{
   display: flex;
   gap:20px;   
}

.socialicon >button{
  border:none;
  font-size:25px;
  margin-top:25px;
}

select{
  width:200px;
  height:30px;
  margin-top: 25px;
}
